import { Fragment, useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useTranslation } from "react-i18next"
import { Skeleton } from "@mui/material"
import Table from "./Table"
import { Row, Col } from "reactstrap"
import StatsHorizontal from "@components/widgets/stats/StatsHorizontal"
import { User, UserPlus, UserCheck, UserX } from "react-feather"
import "@styles/react/apps/app-users.scss"
import { getSummaryData } from "@src/views/apps/user/store"

const UsersList = () => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const store = useSelector((state) => state.users)

  useEffect(() => {
    if (store.dataSummary.length === 0 && !store.isLoadingSummary) {
      dispatch(getSummaryData({}))
    }
  }, [dispatch, store.dataSummary.length])

  return (
    <div className="app-user-list">
      <Row>
        <Col lg="3" sm="6">
          <StatsHorizontal
            color="primary"
            statTitle={t("Total Users")}
            icon={<User size={20} />}
            renderStats={
              store.isLoadingSummary ? (
                <Skeleton
                  sx={{ bgcolor: "#eef2f8" }}
                  animation="wave"
                  height={25}
                  width={40}
                  className="fw-bolder mb-75"
                />
              ) : (
                <h3 className="fw-bolder mb-75">
                  {store.dataSummary.total_data}
                </h3>
              )
            }
          />
        </Col>
        <Col lg="3" sm="6">
          <StatsHorizontal
            color="success"
            statTitle={t("Active Users")}
            icon={<UserCheck size={20} />}
            renderStats={
              store.isLoadingSummary ? (
                <Skeleton
                  sx={{ bgcolor: "#eef2f8" }}
                  animation="wave"
                  height={25}
                  width={40}
                  className="fw-bolder mb-75"
                />
              ) : (
                <h3 className="fw-bolder mb-75">
                  {store.dataSummary.total_active}
                </h3>
              )
            }
          />
        </Col>
        <Col lg="3" sm="6">
          <StatsHorizontal
            color="warning"
            statTitle={t("Pending Users")}
            icon={<UserX size={20} />}
            renderStats={
              store.isLoadingSummary ? (
                <Skeleton
                  sx={{ bgcolor: "#eef2f8" }}
                  animation="wave"
                  height={25}
                  width={40}
                  className="fw-bolder mb-75"
                />
              ) : (
                <h3 className="fw-bolder mb-75">
                  {store.dataSummary.total_suspended}
                </h3>
              )
            }
          />
        </Col>
        <Col lg="3" sm="6">
          <StatsHorizontal
            color="danger"
            statTitle={t("Suspended Users")}
            icon={<UserPlus size={20} />}
            renderStats={
              store.isLoadingSummary ? (
                <Skeleton
                  sx={{ bgcolor: "#eef2f8" }}
                  animation="wave"
                  height={25}
                  width={40}
                  className="fw-bolder mb-75"
                />
              ) : (
                <h3 className="fw-bolder mb-75">
                  {store.dataSummary.total_inactive}
                </h3>
              )
            }
          />
        </Col>
      </Row>
      <Table />
    </div>
  )
}

export default UsersList
